function ConsultView() {

    const [data, setData] = useState(info[0])

    function renderData(id='general'){
        info.map(item=>{
            if(item.id==id){
                setData(item)
            }
        })
    }
    
    return (
        <div className=''>
            {/* phone number and logo*/}
            <div >
                <TopHeader/>
            </div>
            {/* content with background color white */}
            <div className='secondBackground'>   
                {/* navBar*/}
                <div className='navBar-Container'>
                   
                </div>
                {/* services*/}
                

                <div className='services-container' >
                    <div className='bg-img-services d-flex justify-content-center'>
                        <div className='container row d-flex justify-content-center'  >

                            <div className=' col-12 col-xl-3 my-5'>
                                
                                <div className="menu-info border-all">
                                <h2 className='px-1 m-2 border-bottom color-title'>Contenido</h2>
                                <button className='no-btn' onClick={()=> renderData('hepatico')}>-Ultrasonido Hepatico</button>
                                <button className='no-btn' onClick={()=> renderData('pelvico')}>-Ultrasonido Pelvico</button>
                                <button className='no-btn' onClick={()=> renderData('partesBlandas')}>-Ultrasonido Partes Blandas</button>
                                <button className='no-btn' onClick={()=> renderData('mujer')}>-Ultrasonido para Mujer</button>
                                <button className='no-btn' onClick={()=> renderData('hombre')}>-Ultrasonido para Hombre</button>   
                                <button className='no-btn' onClick={()=> renderData('general')}>-Ultrasonido General</button>
                                <button className='no-btn' onClick={()=> renderData('abdomen')}>-Ultrasonido Abdomen Superior</button>
                                <button className='no-btn' onClick={()=> renderData('seno')}>-Ultrasonido de Seno</button>
                                <button className='no-btn' onClick={()=> renderData('vaginal')}>-Ultrasonido Vaginal</button>
                                </div>
                            </div>

                            <div className='col-12 col-xl-9 p-2' >
                                <div className=' border-all my-2 p-3' >
                                    <h2 className='mt-3- d-flex color-title' id={data.id}> {data.title}</h2>
                                    <div className='d-flex justify-content-center align-items-center row'>
                                        <div className='col-lg-12 col-xl-6'>
                                            <p className='info-eco'>{data.description}</p>
                                        </div>
                                        <div className='col-lg-12 col-xl-6 d-flex justify-content-center'> 
                                            <img src={images.bazo} className="img-article info-img"/>
                                        </div>
                                        <div className='d-flex-row justify-content-center align-items-center mt-5 border-top'>
                                            <h5  className='mt-5 color-title'>Ayuda a diagnosticar problemas relacionados a:</h5>
                                            <ul>
                                                {
                                                    data.diagnostic?.map((item, index)=>{
                                                        return(
                                                                <li key={index}>{item}</li>
                                                            )
                                                    })
                                                }
                                            </ul>
                                        </div>
                                    </div>
                                    <div>
                                        <p className="btn btn-secondary">Agendar cita </p>
                                    </div>
                                </div>
                            </div>
                        </div>

                    </div>
                </div>

            
                <div className='footer-container '>
                    <FooterComponent/>
            </div>
            </div>
            
        </div>
    );
}

export default ConsultView;