import React, { useEffect, useState } from "react";
import '../../assets/styles/myStyles.css'
import { images } from '../../constants/images'
import { InfoPackage } from "../../constants/infoPackage";
import { Link } from 'react-router-dom'
import Fade from 'react-reveal/Fade';
import styled, { css } from 'styled-components';
import { Carousel } from '@trendyol-js/react-carousel';

const InfoCard = () => {

    const Item = styled.div`
    display: flex;
    flex-direction: column;
    width:320px;
    justify-content: center;
    align-items: center;
    height: 350px;
    font-family: Signika;
    font-weight: bold;
    border:1px solid #1115;
    padding:0.5em;
    background:#3e5f8a;
    box-shadow: 1px 1px 5px black;
    
    @media (max-width: 576px) {
        width: calc(100%);
        
    }
  `;
    
  const RightArrow = styled.div`
  width: 1px; 
  height: 1px; 
  border-left: 20px solid #0007;
  border-top:15px solid transparent;
  border-bottom: 15px solid transparent;
  margin-left:5px;
`;
const LeftArrow = styled.div`
  width: 1px; 
  height: 1px; 
  border-right: 20px solid #0007;
  border-top: 15px solid transparent;
  border-bottom: 15px solid transparent;
  margin-right:5px;
`;

    const [windowsWidth, setWindowsWidth] = useState(window.innerWidth);
    const [slides, setSlides] = useState(3.5);

   useEffect(()=>{
       setWindowsWidth(window.innerWidth);
       if(windowsWidth < 1600){
        setSlides(2.5);
        }
        if(windowsWidth < 1220){
            setSlides(2);
            }
        if(windowsWidth < 1000){
            setSlides(1.5);
            }
            if(windowsWidth < 780){
                setSlides(1.2);
           }
       if(windowsWidth < 600){
            setSlides(1);
       }
        
   })

   console.log(slides)
    return(
        <div className="container">
            <Fade cascade>
                <Carousel slide={1} show={slides} swiping={true} rightArrow={<RightArrow/>} leftArrow={<LeftArrow/>}>
                {
                    InfoPackage.map(item => {
                        return(
                            
                                <Item>
                                    <h3 className="text-white">{item.title}</h3>
                                    <img src={item.image} className="info-card-img"/>
                                    <Link className="nav-link " to='/consult'><span className="btn-secondary btn-text ">mas información</span></Link>
                                    
                                </Item>
                        )
                    })
                }
                </Carousel>
            </Fade>
                    
                     
        </div> 
    )
}

export default InfoCard