import 'bootstrap/dist/css/bootstrap.min.css';
import { images } from '../constants/images'
import '../assets/styles/myStyles.css'
import Fade from 'react-reveal/Fade';
import { Rerousel } from 'rerousel';
import React, { useRef } from "react";
import styled, { css } from 'styled-components';

// my components
import FooterComponent from '../components/footer/FooterComponent';
import TopHeader from '../components/topHeader/TopHeader';
import Map from '../components/map/Map';
import FlipInfoCard from '../components/flipInfoCard/FlipInfoCard';
import FluidMenu from '../components/fluid-menu/FluidMenu';
import InfoCard from '../components/infoCard/InfoCard';
//cambiar por HomeView

function HomeView() {
    const ref = useRef(null)
    const Item = styled.div`
    display: flex;
    justify-content: center;
    align-items: center;
    width: calc(100%/5);
    height: 2em;
    font-family: Signika;
    font-weight: bold;
    font-size: 1.5em;
    color: black;
    
    
    @media (max-width: 576px) {
        width: calc(100%/2);
        height: 1.5em;
    }
  `;

  

  return (
    <div className=''>
      {/*Fluid meni*/}
       <FluidMenu/>

        {/* phone number and logo*/}
        <div  >
            <TopHeader/>
        </div>

        {/* content with background color white */}
        <div className='secondBackground pt-5' >   
            <div className='article-container'>
                {/* info */}
                <div className="article-text-container row " >
                    <Fade cascade>
                        <div className="d-flex-row justify-content-center align-items-center mb-2 col-md-12 col-lg-6 col-xl-7">
                            <h1 className='text-justify m-1 color-title' >Cuando se Recomienda una Ecografía Obstétrica?</h1>
                            <p className='info-eco'>La primera ecografía suele realizarse a las 12 semanas de gestación, para dar tiempo al embrión a ir desarrollándose. Sin embargo, quienes tengan contratado un seguro médico de salud, es posible solicitar una cita con el especialista y realizar la prueba en la semana 6 del embarazo. No obstante, las ecografías tempranas no muestran el latido del feto e incluso, podrían llegar a mostrar dos embriones, cuando a las 12 semanas solo se mostraría uno. Por este motivo, los ginecólogos recomiendan la primera ecografía tras el primer trimestre.</p>
                        </div>
                    </Fade>
                    
                    <div className='col-sm-12 col-xl-5 bg-img-article' >
                    <Fade cascade>
                        <div>
                            <img src={images.ultra4d} className="img-article "/>
                        </div>
                    </Fade>
                        
                    </div>
                </div>

                <div className="article-text-container row" >
                    <div className='col-sm-12 col-xl-5 bg-img-article'>
                    <Fade cascade>
                            <div>
                                <img src={images.renal} className="img-article "/>   
                            </div>
                    </Fade>
                        
                    </div>
                    <Fade cascade>
                        <div className="d-flex-row justify-content-center align-items-center mb-2 col-md-12 col-lg-7 col-xl-7">
                            <h1 className='text-justify m-1 color-title' >Problemas Renales</h1>
                            <p className='info-eco'>Algunos problemas de los riñones pueden ser signos tempranos de enfermedad renal crónica, el tipo de daño que puede empeorar después de un tiempo y puede resultar en falla renal.  Detectar y tratar estos problemas puede ayudar a prevenir que enfermedad renal crónica se convierta en falla renal.  Otros problemas de los riñones pueden resultar en enfermedad renal crónica si no son controlados.  Conocer tu cuerpo y hablar con tu proveedor de salud cuando notas que algo no está bien para que te ayude a prevenir problemas más graves en el futuro.</p>
                        </div>
                    </Fade>
                   
                </div>
            </div>
            <div className='icon-slide-container'>
                <Rerousel itemRef={ref}>
                    <Item ref={ref}><img src={images.questions} className='slide-image'/></Item>
                    <Item><img src={images.aboutUs} className='slide-image'/></Item>
                    <Item><img src={images.services} className='slide-image'/></Item>
                    <Item><img src={images.localization} className='slide-image'/></Item>
                    <Item><img src={images.mision} className='slide-image'/></Item>
                    <Item><img src={images.date} className='slide-image'/></Item>
                    <Item><img src={images.emergy} className='slide-image'/></Item>
                    <Item><img src={images.email} className='slide-image'/></Item>
                </Rerousel>
            </div>

            {/* services*/}
            <div className='services-container'>
                <h1 className=" d-flex justify-content-center my-4 color-title ">Servicios</h1>
                <div className='bg-img-services '>
                        {/*
                            <FlipInfoCard />
                        */}
                        <div className='my-2'>
                            <InfoCard/>
                        </div>
                        <div className='my-2'>
                            <InfoCard/>
                        </div>
                        <div className='px-3'>
                            <Map/>
                        </div>
                </div>
            </div>

            {/* footer*/}
            <footer className='footer-container'>
               <FooterComponent/>
            </footer>

        </div>
    </div>
  );
}

export default HomeView;
