import React from "react";
import { Link } from 'react-router-dom'
import { images } from "../../constants/images";

const NavBar = () => {
    return(
        <div className="">
            <nav className="navbar navbar-expand-lg navbar-dark nav-color " id="myMenu">
                <div className="container-fluid">
                    <Link className="navbar-brand" to='/'><span ><img src={images.logo} className='nav-image'/> <div className="progress-bar"></div></span></Link>
                    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNavAltMarkup" aria-controls="navbarNavAltMarkup" aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"></span>
                    </button>
                    <div className="collapse navbar-collapse" id="navbarNavAltMarkup">
                    <div className="navbar-nav">
                        <Link className="nav-link active " aria-current="page" to='/about'><span className="my-btn-nav">Quienes somos? <div className="progress-bar"></div></span></Link>
                        <Link className="nav-link " to='/consult'><span className="my-btn-nav">Servicios <div className="progress-bar"></div></span></Link>
                        <Link className="nav-link " to='/fqa'><span className="my-btn-nav">Preguntas Frecuentes <div className="progress-bar"></div></span></Link>
                    </div>
                    </div>
                </div>
            </nav>
        </div>
    )
}

export default NavBar