import 'bootstrap/dist/css/bootstrap.min.css';
import { images } from '../constants/images'
import '../assets/styles/myStyles.css'
import FluidMenu from '../components/fluid-menu/FluidMenu';
import NavBar from '../components/navbar/NavBar';
import TopHeader from '../components/topHeader/TopHeader';
import FooterComponent from '../components/footer/FooterComponent';
import Map from '../components/map/Map';
//cambiar por HomeView

function AboutUsView() {
  return (
    <div className=''>
        {/* phone number and logo*/}
        <FluidMenu/>
        <div >
            <TopHeader/>
        </div>
        {/* content with background color white */}
        <div className='secondBackground'>   
            {/* navBar*/}
            <div className='navBar-Container'>
                
            </div>
            {/* services*/}
            <div className='container mt-5 mb-5'>
                <div className="pt-4 w-75 m-auto d-flex-row justify-content-center" >
                    <h1>Quienes somos?</h1>
                    <p className='info-eco'>Somos una clínica enfocada en brindar servicios de prevención y diagnostico de salud de la población mexicana mediante tecnologías de ultrasonografía. Este es el método de imagen medica más utilizado por radiólogos, cardiólogos, medicina deportiva y fisioterapia, ya que presenta ventajas de no ser invasivo, rápido, económico, fácil de utilizar, preciso y seguro. La mayoría de los casos clínicos que se presentan son necesario estudios de laboratorio que involucran análisis con ultrasonido para el seguimiento adecuado, por lo que brindar precios accesibles y ofrecer atención de calidad son nuestra prioridad.</p>
                </div>
                <div className="pt-4 w-75 m-auto d-flex-row justify-content-center" >
                    <div className='d-flex align-items-center'>
                        <p className='h1 m-4'>Mision</p>
                        <img src={images.mision} style={{width:"50px"}}/>
                    </div>
                    <p className='info-eco'> Contribuir a la prevención y seguimiento de tratamientos de padecimientos de la población mexicana que involucran estudios de ultrasonido, para mejorar el bienestar social y la calidad de vida. Además, ofrecer servicios móviles a domicilio para pacientes incapacitados de movilidad o difícil acceso a clínicas convencionales.  </p>
                </div>
                <div className="pt-4 w-75 m-auto d-flex-row justify-content-center">
                    <div className='d-flex align-items-center'>
                        <p className='h1 m-4'>Vision</p>
                        <img src={images.vision} style={{width:"70px"}}/>
                    </div>
                    <p className='info-eco'>Ser la clínica proveedora de servicios de ultrasonografía de mejor calidad, atención y económica número uno en México.</p>
                    </div> 
                    <div className='map-container-full '>
                <div className="container mt-5 mb-5" >
                    <div className='text-center ' >
                        <h1 className=''>Nos ubicamos</h1>
                    </div>
                    <div className="container p-3 row d-flex justify-content-center m-auto" >
                        <div className='d-flex justify-content-center align-items-center col-12' >
                            <div className="">
                                <iframe  className= "map "src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3713.1326781786897!2d-104.83888768505999!3d21.46331008576455!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x0%3A0x8576928539d0e467!2zMjHCsDI3JzQ3LjkiTiAxMDTCsDUwJzEyLjEiVw!5e0!3m2!1ses!2smx!4v1644616016367!5m2!1ses!2smx"  allowFullScreen loading="lazy"></iframe>
                            </div>
                        </div>
                    </div>
                </div>
                </div>
            </div>
            <div className='footer-container'>
                <FooterComponent/>
            </div>
        </div>
    </div>
  );
}

export default AboutUsView;
