import React from "react";
import '../../assets/styles/myStyles.css'
import { images } from '../../constants/images'
import { Link } from 'react-router-dom'
import Fade from 'react-reveal/Fade';

const FooterComponent = () => {
    return(
        <footer className=" text-white text-center text-lg-start">
        <Fade cascade>
          <div className="container p-4">
            
            <div className="row">
              
              <div className="col-lg-6 col-md-12 mb-4 mb-md-0">
              <h5 className="text-uppercase m-3 card-title">Ultra Fusion</h5>
                  <img src={images.logo} width="75px"/>
                  
                  <p className="m-3 card-title">
                    Somos una clínica enfocada en brindar servicios de prevención y diagnostico de salud de la población mexicana mediante tecnologías de ultrasonografía. 
                  </p>
              </div>
            
              <div className="col-lg-3 col-md-6 mb-4 mb-md-0">
                <h5 className="text-uppercase">Servicios</h5>
        
                <ul className="list-unstyled mb-0">
                  <li>
                  <Link className="nav-link " to='/consult'><span className="text-white btn-text">Ultrasonido Partes Blandas</span></Link>
                  </li>
                  <li>
                  <Link className="nav-link " to='/consult'><span className="text-white btn-text">Ultrasonido Hepatico</span></Link>
                  </li>
                  <li>
                  <Link className="nav-link " to='/consult'><span className="text-white btn-text">Ultrasonido General</span></Link>
                  </li>
                  <li>
                  <Link className="nav-link " to='/consult'><span className="text-white btn-text">Ultrasonido Pelvico</span></Link>
                  </li>
                </ul>
              </div>
              
              <div className="col-lg-3 col-md-6 mb-4 mb-md-0">
                <h5 className="text-uppercase mb-0">Conocenos</h5>
        
                <ul className="list-unstyled">
                  <li>
                  <Link className="nav-link " to='/about'><span className="text-white btn-text">Quienes Somos?</span></Link>
                  </li>
                  <li>
                  <Link className="nav-link " to='/about'><span className="text-white btn-text">Misión y Visión</span></Link>
                  </li>
                  <li>
                  <a href="mailto:humbertomacias83@gmail.com?Subject=Interesado%20en%20el%20paquete%20de%20ecografia%20..." className="p-4 text-white" >
                  humbertomacias83@gmail.com
                                  </a>
                  </li>
                </ul>
              </div>
              
            </div>
            
          </div>
        </Fade>
      
       
        <div className="text-center p-3" style={{backgroundColor:"rgba(0, 0, 0, 0.2)"}}>
          © 2022 Copyright:
          <a className="text-white" href="#"> Ultra Fusion</a>
        </div>
        
      </footer>
    )
}

export default FooterComponent