import React, { useRef } from "react";
import { images } from '../../constants/images'
import '../../assets/styles/myStyles.css'
import NavBar from "../navbar/NavBar";
import styled, { css } from 'styled-components';
import Fade from 'react-reveal/Fade';

const TopHeader = () => {
    const ref = useRef(null)
    const Item = styled.div`
    display: flex;
    justify-content: center;
    align-items: center;
    width: calc(100%/2);
    height: 1em;
    font-family: Signika;
    font-weight: bold;
    font-size: 1.5em;
    color: white;
    background-color: #000000;
    
    @media(max-width: 1150px) {
        width: 100%
    }
  `;
  const Color1 = styled.div`
    width:100%;
    height:100%;
    background-image: linear-gradient(to right, red , yellow);
  `;
  const Color2 = styled.div`
    width:100%;
    height:100%;
    background-image: linear-gradient(to right, yellow , red);
  `;


    return(
        <div className="d-flex-row" >
            <div className='navBar-Container' >
                <NavBar/>
            </div>
            
            <div className='window-image-background' >
                <div className="box-text-banner">
                    <Fade left big>
                        <span className="title-banner">Lorem Ipsum is simply dummy text of the printing and typesetting industry. </span>
                    </Fade>
                </div>
            </div>
            <div className='' >
            
            </div>
        </div>
    )
}


export default TopHeader